import React, { useState, useContext, useEffect } from "react";
import { Context } from "../context/context";
import "../styles/contact.css";
import MailOutlineIcon from "@mui/icons-material/MailOutline";

const PopUpButton = (props) => {
	const { footerInView } = useContext(Context);
	const [showMailIcon, setShowMailIcon] = useState(false);
	const { setIsContact } = useContext(Context);

	useEffect(() => {
		let btn = document.getElementById("side-button");
		if (footerInView === true) {
			btn.style.animation = "slideDown 0.2s ease";
			setTimeout(() => {
				btn.style.display = "none";
			}, 200);
		} else if (footerInView === false) {
			btn.style.display = "block";
			btn.style.animation = "slideUp 0.2s ease";
		}
		if (props.caller === "home") {
			if (props.event.inView === false) {
				btn.className = "";
				btn.classList.add("mail-button");
				setTimeout(() => {
					setShowMailIcon(true);
				}, 150);
			} else {
				btn.className = "";
				btn.classList.add("home-button");
				btn.style.display = "block";
				setTimeout(() => {
					setShowMailIcon(false);
				}, 150);
			}
			return;
		}
	}, [props, footerInView]);

	return (
		<button
			id="side-button"
			className="home-button"
			onClick={() => setIsContact(true)}
		>
			{showMailIcon ? <MailOutlineIcon sx={{ fontSize: 44 }} /> : "LET'S TALK!"}
		</button>
	);
};

export default PopUpButton;
